
import { defineComponent } from 'vue'
import ScoreboardComp from "./Scoreboard.vue"

interface PersonalBestScore{
    pb: number;
    pbSec: number;
    uniqueIndex: number;
    firstGame: boolean;
}

export default defineComponent({
    name: "ResultsScreen",

    emits: [
        "resultsMounted"
    ],

    components: {
        ScoreboardComp
    },

    data(){
        return{
            resultItems: [
                {
                    name: "Black Hole",
                    lifespan: "",
                    size: "",
                    examples: "",
                    funFacts: [
                        "",
                        "",
                        ""
                    ]
                },
                {
                    name: "Pulsar",
                    lifespan: "",
                    size: "",
                    examples: "",
                    funFacts: [
                        "",
                        "",
                        ""
                    ]
                },
                {
                    name: "Blue Giant",
                    lifespan: "",
                    size: "",
                    examples: "",
                    funFacts: [
                        "",
                        "",
                        ""
                    ]
                },
                {
                    name: "Red Giant",
                    lifespan: "",
                    size: "",
                    examples: "",
                    funFacts: [
                        "",
                        "",
                        ""
                    ]
                },
                {
                    name: "Yellow Dwarf",
                    lifespan: "",
                    size: "",
                    examples: "",
                    funFacts: [
                        "",
                        "",
                        ""
                    ]
                },
                {
                    name: "Red Dwarf",
                    lifespan: "10 days",
                    size: "1 km",
                    examples: "XxX",
                    funFacts: [
                        "FunFact1",
                        "FunFact2",
                        "FunFact3"
                    ]
                },

            ],

            currResItem: 0,
            randNum: 0,
            currResName: "",
            currResLifespan: "",
            currResSize: "",
            currResExamples: "",
            currResFunFact: "",

            // Personal Best Scores
            personalBestScores: [] as PersonalBestScore[],
            scoreIndex: 0,
            newPB: false
        }
    },

    props:[
        "finishScreen",
        "timeDuration",
        "gameScore",
        "totalClicks"
    ],

    mounted(){
        this.$emit("resultsMounted");
    },

    methods:{

        resultInfo(){

            switch (true){
                case this.scorePerSecond >= 2.75:
                    this.currResItem = 0;
                    break;

                case this.scorePerSecond >= 2.25:
                    this.currResItem = 1;
                    break;

                case this.scorePerSecond >= 1.75:
                    this.currResItem = 2;
                    break;

                case this.scorePerSecond >= 1.25:
                    this.currResItem = 3;
                    break;

                case this.scorePerSecond >= 0.75:
                    this.currResItem = 4;
                    break;

                case this.scorePerSecond < 0.75:
                    this.currResItem = 5;
                    break;
            }

            type ResultItem = {
                name: string;
                lifespan: string;
                size: string;
                examples: string;
                funFacts: string[];
            };

            const currRes:ResultItem = this.resultItems[this.currResItem];

            // Name
            this.currResName = currRes.name;

            // Lifespan
            this.currResLifespan = currRes.lifespan;

            // Size
            this.currResSize = currRes.size;

            // Examples
            this.currResExamples = currRes.examples;

            // Random Fun Fact
            this.randNum = Math.floor(Math.random() * 3);
            this.currResFunFact = currRes.funFacts[this.randNum];

            // Save results function in the Scoreboard component
            (this.$refs.saveResults as InstanceType<typeof ScoreboardComp>).saveResults();

        },

        pbScores(pbScoresArray: PersonalBestScore[]){
            
            this.personalBestScores = pbScoresArray;

            switch (this.timeDuration){
                case 10:
                    this.scoreIndex = 0;
                    break;
                case 30:
                    this.scoreIndex = 1;
                    break;
                case 60:
                    this.scoreIndex = 2;
                    break;
            }

            const currScores = this.personalBestScores[this.scoreIndex];

            // Check if it's a personal record, so if:
            // The current score is the highest in the scoreboard (current game duration)
            // It's unique, so the score must be better than the previous one
            // It's not a first game
            if (currScores.pb == this.gameScore &&
                currScores.uniqueIndex <= 1 &&
                currScores.firstGame === false){
                this.newPB = true;
            } else {
                this.newPB = false;
            }

        }

    },

    computed:{

        scorePerSecond(){
            return this.gameScore / this.timeDuration;
        },

        accuracy(){
            if (this.totalClicks){
                return this.gameScore / this.totalClicks;
            } else {
                return 0;
            }
        },
        
        CPS(){
            return this.totalClicks / this.timeDuration;
        },

        missedClicks(){
            return this.totalClicks - this.gameScore;
        }

    }

})
